.skills {
  width: 100%;
  position: relative;
  background-color: #fff;
  padding-top: 40px;
  padding-bottom: 40px;
  color: #032B2F;

  h1 {
    font-size: 1.6rem;
    text-align: center;
    font-weight: 700;
    margin-bottom: 40px;
  }

  .skills_wrapper {
    width: 100%;
    max-width: 980px;
    margin: 0 auto;

    section {
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      width: 100%;
      height: 150px;
      position: relative;
      z-index: 0;
      color: #fff;
      padding: 10px 15px 0 15px;
      margin-bottom: 40px;

      h2 {
        font-size: 1.6rem;
        text-align: center;
      }

      ul {
        display: flex;
        justify-content: space-between;
        li {
          list-style: none;
          margin-top: 30px;

          img {
            width: 40px;
            height: 40px;
          }

          span {
            display: none;
          }
        }

      }
    }
    .skills_frontend {
      background-image: url("../images/frontend2.jpg");
    }
    .skills_backend {
      background-image: url("../images/backend.jpg");
    }
    .skills_other {
      background-image: url("../images/frontend.jpg");
    }
    section::after {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      background-color: rgba(1, 1, 1, 0.5);
      z-index: -1;
    }
    .skills_line {
      width: 150px;
      height: 2px;
      background-color: #032B2F;
      margin: 0 auto 40px auto;
    }
  }
}

@media screen and (min-width: 900px) {
  .skills {
    padding-left: 10px;
    padding-right: 10px;
    .skills_wrapper {
      display: flex;
      justify-content: space-between;
      section {
        width: 30%;
        height: 360px;

        ul {
          flex-direction: column;
          align-items: flex-start;
          justify-content: space-between;

          li{
            span {
              display: inline-block;
              font-size: 0.8rem;
              margin-left: 10px;
              position: relative;
              bottom: 13px;
            }
          }
        }
      }
      .skills_line {
        display: none;
      }
    }
  }
}
